import { DataQuery, DataSourceJsonData } from '@grafana/data';

export interface MyQuery extends DataQuery {
  topic: string;
  manualTopic?: string;
  hasManualTopic: boolean;
  pointId?: string;
  member: string;
  legend: string;
  messaging: Messaging;
  hasResponseSelector: boolean;
}

export enum Messaging {
  DEFAULT = 'DEFAULT',
  REQ_RES = 'REQ_RES',
}

export const defaultQuery: Partial<MyQuery> = {
  topic: '',
  manualTopic: '',
  hasManualTopic: false,
  pointId: '',
  member: 'value',
  legend: 'value',
  messaging: Messaging.DEFAULT,
  hasResponseSelector: true,
};

/**
 * These are options configured for each DataSource instance
 */
export interface MyDataSourceOptions extends DataSourceJsonData {
  protocol: string;
  host: string;
  port: number;
  authentication: boolean;
  username: string;
  password: string;
  deviceId: string;

  defaultTextColor: string;
  trueTextColor: string;
  textSize: number;

  defaultUnitColor: string;
  trueUnitColor: string;
  unitSize: number;

  switchTrueColor: string;
  switchFalseColor: string;

  buttonBackgroundColor: string;
  buttonFontColor: string;
  buttonBackgroundHoverColor: string;
  buttonFontHoverColor: string;
  buttonBackgroundDisabledColor: string;
  buttonFontDisabledColor: string;

  sliderColor: string;

  opacity: number;
  scale: number;
  xPosition: number;
  yPosition: number;
}

export const defaultValues: MyDataSourceOptions = {
  protocol: 'ws',
  host: '',
  port: 9001,
  authentication: false,
  username: '',
  password: '',
  deviceId: '',

  textSize: 40,
  defaultTextColor: '',
  trueTextColor: '',

  unitSize: 20,
  defaultUnitColor: '',
  trueUnitColor: '',

  switchTrueColor: '#52D869',
  switchFalseColor: '#7E7E7E',

  buttonBackgroundColor: '#303F9F',
  // buttonFontColor: '#FFFFFF',
  buttonFontColor: '#E0E0E0',
  buttonBackgroundHoverColor: '#30409E',
  // buttonFontHoverColor: '#FFFFFF',
  buttonFontHoverColor: '#E0E0E0',
  buttonBackgroundDisabledColor: '#E0E0E0',
  buttonFontDisabledColor: '#AEAEAE',

  sliderColor: '#303F9F',

  opacity: 30,
  scale: 30,
  xPosition: 100,
  yPosition: 0,
};
