import React, { ChangeEvent, PureComponent, SyntheticEvent } from 'react';
import defaults from 'lodash/defaults';
import { ColorPicker, LegacyForms, stylesFactory } from '@grafana/ui';
import { css } from 'emotion';
import { DataSourcePluginOptionsEditorProps, SelectableValue } from '@grafana/data';
import { defaultValues, MyDataSourceOptions } from './types';
import { SliderEditor } from './components/SliderEditor';

const { Switch, Select, Input } = LegacyForms;

interface Props extends DataSourcePluginOptionsEditorProps<MyDataSourceOptions> {}

interface State {}

const protocolOptions: Array<SelectableValue<string>> = [
  { label: 'ws', value: 'ws' },
  { label: 'wss', value: 'wss' },
];

export class ConfigEditor extends PureComponent<Props, State> {
  onChangeProtocol = (item: SelectableValue<string>) => {
    this.onInputChange('protocol', item.value);
  };

  onHostChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('host', event.target.value);
  };

  onPortChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('port', Number(event.target.value));
  };

  onAuthenticationChange = (event: SyntheticEvent<HTMLInputElement>) => {
    const authentication = (event.target as HTMLInputElement).checked;
    this.onInputChange('authentication', authentication);
  };

  onUsernameChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('username', event.target.value);
  };

  onPasswordChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('password', event.target.value);
  };

  onDeviceIdChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.onInputChange('deviceId', event.target.value);
  };

  onInputChange(key: string, value: any) {
    const { onOptionsChange, options } = this.props;
    const jsonData = {
      ...options.jsonData,
      [key]: value,
    };
    onOptionsChange({ ...options, jsonData });
  }

  render() {
    const { options } = this.props;
    const { jsonData } = options;
    const values = defaults(jsonData, defaultValues);
    const styles = getStyles();
    return (
      <div className="gf-form-group">
        <div className="gf-form-inline">
          <div className="gf-form width-10">
            <Select
              className={'width-10'}
              options={protocolOptions}
              onChange={this.onChangeProtocol}
              value={{
                value: values.protocol,
                label: `Protocol (${values.protocol})`,
              }}
            />
          </div>
          <div className="gf-form max-width-16">
            <Input placeholder="Host" onChange={this.onHostChange} value={values.host} />
          </div>
          <div className="gf-form max-width-4">
            <Input placeholder="Port" type="number" onChange={this.onPortChange} value={values.port} />
          </div>
        </div>
        <div className="gf-form">
          <Switch label="Authentication" checked={values.authentication} onChange={this.onAuthenticationChange} />
        </div>
        {values.authentication && (
          <div className="gf-form-inline">
            <div className="gf-form max-width-15">
              <Input placeholder="Username" onChange={this.onUsernameChange} value={values.username} />
            </div>
            <div className="gf-form max-width-15">
              <Input placeholder="Password" type="password" onChange={this.onPasswordChange} value={values.password} />
            </div>
          </div>
        )}
        <div className="gf-form max-width-30">
          <Input
            placeholder="Device ID (leave blank if you want no subscription)"
            onChange={this.onDeviceIdChange}
            value={values.deviceId}
          />
        </div>

        {this.renderDefaultTextSettings(styles, values)}
        {this.renderDefaultSwitchColorSettings(styles, values)}
        {this.renderDefaultButtonColorSettings(styles, values)}
        {this.renderSliderColorSettings(styles, values)}
        {this.renderDefaultUnitsSettings(styles, values)}
        {this.renderDefaultImageSettings(styles, values)}
      </div>
    );
  }

  private renderDefaultSwitchColorSettings(styles: any, values: any) {
    return (
      <>
        <div className={styles.header}>Default Switch Color Settings</div>

        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>True Color</div>
            <ColorPicker
              color={values.switchTrueColor}
              onChange={color => {
                this.onInputChange('switchTrueColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>False Color</div>
            <ColorPicker
              color={values.switchFalseColor}
              onChange={color => {
                this.onInputChange('switchFalseColor', color);
              }}
            />
          </div>
        </div>
      </>
    );
  }

  private renderDefaultButtonColorSettings(styles: any, values: any) {
    return (
      <>
        <div className={styles.header}>Default Button Color Settings</div>

        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Background Color</div>
            <ColorPicker
              color={values.buttonBackgroundColor}
              onChange={color => {
                this.onInputChange('buttonBackgroundColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Font Color</div>
            <ColorPicker
              color={values.buttonFontColor}
              onChange={color => {
                this.onInputChange('buttonFontColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Background Hover Color</div>
            <ColorPicker
              color={values.buttonBackgroundHoverColor}
              onChange={color => {
                this.onInputChange('buttonBackgroundHoverColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Font Hover Color</div>
            <ColorPicker
              color={values.buttonFontHoverColor}
              onChange={color => {
                this.onInputChange('buttonFontHoverColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Background Disabled Color</div>
            <ColorPicker
              color={values.buttonBackgroundDisabledColor}
              onChange={color => {
                this.onInputChange('buttonBackgroundDisabledColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Font Disabled Color</div>
            <ColorPicker
              color={values.buttonFontDisabledColor}
              onChange={color => {
                this.onInputChange('buttonFontDisabledColor', color);
              }}
            />
          </div>
        </div>
      </>
    );
  }

  private renderSliderColorSettings(styles: any, values: any) {
    return (
      <>
        <div className={styles.header}>Default Slider Color Settings</div>

        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Slider Color</div>
            <ColorPicker
              color={values.sliderColor}
              onChange={color => {
                this.onInputChange('sliderColor', color);
              }}
            />
          </div>
        </div>
      </>
    );
  }

  private renderDefaultTextSettings(styles: any, values: any) {
    return (
      <>
        <div className={styles.header}>Default Text Settings</div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Default Text Color</div>
            <ColorPicker
              color={values.defaultTextColor}
              onChange={color => {
                this.onInputChange('defaultTextColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>True Text Color</div>
            <ColorPicker
              color={values.trueTextColor}
              onChange={color => {
                this.onInputChange('trueTextColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.label}>Text Size</div>
          <div className="max-width-4">
            <Input
              placeholder="Text Size"
              type="number"
              onChange={e => {
                this.onInputChange('textSize', Number(e.target.value));
              }}
              value={values.textSize}
              min={1}
            />
          </div>
        </div>
      </>
    );
  }

  private renderDefaultUnitsSettings(styles: any, values: any) {
    return (
      <>
        <div className={styles.header}>Default Unit Settings</div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>Default Unit Color</div>
            <ColorPicker
              color={values.defaultUnitColor}
              onChange={color => {
                this.onInputChange('defaultUnitColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.inline}>
            <div className={styles.label}>True Unit Color</div>
            <ColorPicker
              color={values.trueUnitColor}
              onChange={color => {
                this.onInputChange('trueUnitColor', color);
              }}
            />
          </div>
        </div>
        <div className="gf-form">
          <div className={styles.label}>Text Size</div>
          <div className="max-width-4">
            <Input
              placeholder="Unit Size"
              type="number"
              onChange={e => {
                this.onInputChange('unitSize', Number(e.target.value));
              }}
              value={values.unitSize}
              min={1}
            />
          </div>
        </div>
      </>
    );
  }

  private renderDefaultImageSettings(styles: any, values: any) {
    return (
      <>
        <div className={styles.header}>Default Background Image Settings</div>
        <div className="gf-form max-width-30">
          <SliderEditor
            label="Opacity"
            value={values.opacity}
            onChange={value => {
              this.onInputChange('opacity', value);
            }}
          />
        </div>
        <div className="gf-form max-width-30">
          <SliderEditor
            label="Scale"
            value={values.scale}
            onChange={value => {
              this.onInputChange('scale', value);
            }}
          />
        </div>
        <div className="gf-form max-width-30">
          <SliderEditor
            label="X-Position"
            value={values.xPosition}
            onChange={value => {
              this.onInputChange('xPosition', value);
            }}
          />
        </div>
        <div className="gf-form max-width-30">
          <SliderEditor
            label="Y-Position"
            value={values.yPosition}
            onChange={value => {
              this.onInputChange('yPosition', value);
            }}
          />
        </div>
      </>
    );
  }
}

const getStyles = stylesFactory(() => {
  return {
    header: css`
      font-size: 16px;
      margin: 32px 0 8px;
      font-weight: 500;
    `,
    inline: css`
      height: 32px;
      display: flex;
      direction: column;
      align-items: center;
    `,
    label: css`
      width: 170px;
      height: 32px;
      background-color: #f1f5f9;
      display: flex;
      align-items: center;
      padding: 0 8px;
      border-radius: 3px;
      font-weight: 500;
      font-size: 12px;
      margin-right: 4px;
    `,
  };
});
