import React from 'react';
import { Slider, stylesFactory } from '@grafana/ui';
import { css } from 'emotion';

interface Props {
  label: string;
  onChange: (value: number) => void;
  value: number;
}

export const SliderEditor: React.FC<Props> = props => {
  const styles = getStyles();

  return (
    <div className={styles.wrapper}>
      <div className={styles.label}>{props.label}</div>
      <div className={styles.slider}>
        <Slider
          tooltipAlwaysVisible={false}
          min={0}
          max={100}
          onAfterChange={values => {
            props.onChange(values[0]);
          }}
          value={[props.value]}
        />
      </div>
    </div>
  );
};

const getStyles = stylesFactory(() => {
  return {
    wrapper: css`
      display: flex;
      flex-direction: row;
      width: 100%;
      padding: 0;
    `,
    label: css`
      background-color: #f1f5f9;
      width: 74px;
      display: flex;
      align-items: center;
      padding: 0 8px;
      border-radius: 3px;
      font-weight: 500;
      font-size: 12px;
      margin-right: 4px;
    `,
    slider: css`
      margin-top: -15px;
      display: flex;
      flex: 1;
    `,
  };
});
